
#request-wrapper {
  position:absolute;
  margin:0;
  margin-top:-40px;
  width:100%;
  height:100%;
  background-image: url(./../assets/login-background.png);
  background-size: cover;
}
.request-box {
  margin-top:80px;width:600px;height:450px;position:relative;background-color:#e0e0e0;border:1px solid black;
  box-shadow:6px 6px 0px 0px rgba(48,48,48,.5);
}
.login-header {
  position:relative;width:100%;float:left;text-align:center;margin-bottom:25px;
}
#pai-logo-login {
  position:relative;width:210px;margin-top:25px;margin-bottom:-5px;
}
.request-form {
  position:relative;width:100%;float:left;text-align:center;
}
.request-form table {
  border-spacing: 0;
  width:90%;
}
.request-form tr td {
  padding: 0px;
  margin-top: 10px;
  text-align:center;
  width: 50%;
}
.request-form textarea {
  width:97%;
  height:80px;
  margin-top:5px;
  resize: none;
  font-size:17px;
}
.request-form input {
  width:95%;
  height:25px;
  font-size: 18px;
  margin-top:5px;
  vertical-align: 25px;
}
.request-form select {
  width:95%;
  height:25px;
  font-size: 18px;
  margin-top:15px;
}
#request-button {
  width:200px;height:40px;background-color:#779c4c;color:white;font-family:verdana;font-size:23px;border:1px solid black;margin-top:10px;
}
.phone-boxes {
  width:100%;
  margin-top: 10px;
  margin-left:4px;
}



