
.embroidery-form {
  position:absolute;
  margin-top: 10px;
  width:100%;
  background-color: white;
}
.embroidery-form table {
  width:80%;
  border-collapse: collapse;
  background-color: white;
  border: 2px solid black;
}
.embroidery-form table tr td {
  height:40px;
  width: 40%;
  border-bottom: 1px dotted grey;
}
.embroidery-form table tr td:nth-child(3) {
  height:40px;
  width: 20%;
  border-bottom: 1px dotted grey;
}
.embroidery-form table select {
  height:20px;
  width:80px;
  vertical-align: bottom;
  font-size:14px;
}
.embroidery-form table div, input {
  width:45%;
  float:left;
}
.text-area-div {
  padding-left:2%;
  padding-top: 1%;
  width:78%;
  height:200px;
  background-color: #e0e0e0;
}
.text-area-div textarea {
  width:48%;height:90%;float:left;margin:4px;max-height:90%;max-width:48%;
}
.embroidery-form input[type=submit] {
  width:300px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 20px;
  float:none;
}



